.btn,
.navbar .navbar-nav > a.btn {
    border-width: $border-thick;
    border: none;
    position: relative;
    overflow: hidden;
    margin: 4px 1px;
    border-radius: $border-radius-lg;
    cursor: pointer;

    @include btn-styles($default, $default-states);

    &:hover,
    &:focus {
        @include opacity(1);

        outline: 0 !important;
    }

    &:active,
    &.active,
    .open > &.dropdown-toggle {
        @include box-shadow(none);

        outline: 0 !important;
    }

    .badge {
        margin: 0;
    }

    &.btn-icon {
        // see above for color variations
        height: $icon-size-regular;
        min-width: $icon-size-regular;
        width: $icon-size-regular;
        padding: 0;
        font-size: $icon-font-size-regular;
        overflow: hidden;
        position: relative;
        line-height: normal;

        &.btn-simple {
            padding: 0;
        }

        &.btn-sm {
            height: $icon-size-sm;
            min-width: $icon-size-sm;
            width: $icon-size-sm;

            .fa,
            .far,
            .fas,
            .fad,
            .fal,
            .tim-icons {
                font-size: $icon-font-size-sm;
            }
        }

        &.btn-lg {
            height: $icon-size-lg;
            min-width: $icon-size-lg;
            width: $icon-size-lg;

            .fa,
            .far,
            .fas,
            .fad,
            .fal,
            .tim-icons {
                font-size: $icon-font-size-lg;
            }
        }

        &:not(.btn-footer) {
            .tim-icons,
            .fa,
            .far,
            .fad,
            .fal,
            .fas {
                position: absolute;
                font-size: 1em;
                top: 50%;
                left: 50%;
                transform: translate(-12px, -12px);
                line-height: 1.5626rem;
                width: 24px;
            }
        }
    }

    &:not(.btn-icon) .tim-icons {
        position: relative;
        top: 1px;
    }

    span {
        position: relative;
        display: block;
    }

    &.btn-link.dropdown-toggle {
        color: $dark-gray;
    }

    &.dropdown-toggle:after {
        margin-left: 30px !important;
    }
}

@each $color, $value in $theme-colors {
    .btn-#{$color} {
        @include button-variant($value, $value);
    }
}

@each $color, $value in $theme-colors {
    .btn-outline-#{$color} {
        @include button-outline-variant($value);
    }
}

// Apply the mixin to the buttons
// .btn-default { @include btn-styles($default-color, $default-states-color); }
.btn-primary {
    @include btn-styles($primary, $primary-states);
}

.btn-success {
    @include btn-styles($success, $success-states);
}

.btn-info {
    @include btn-styles($info, $info-states);
}

.btn-warning {
    @include btn-styles($warning, $warning-states);

    &:not(:disabled):not(.disabled):active {
        color: $white;
    }
}

.btn-danger {
    @include btn-styles($danger, $danger-states);
}

.btn-neutral {
    @include btn-styles($white, $white);
}

.btn {
    &:disabled,
    &[disabled],
    &.disabled {
        @include opacity(0.5);

        pointer-events: none;
    }
}

.btn-simple {
    border: $border;
    border-color: $default;
    box-shadow: none;
    padding: ($padding-btn-vertical - 1) ($padding-round-horizontal - 1);
    background-color: $transparent-bg;
}

.btn-simple,
.btn-link {
    &.disabled,
    &:disabled,
    &[disabled],
    fieldset[disabled] & {
        &,
        &:hover,
        &:focus,
        &.focus,
        &:active,
        &.active {
            background: $transparent-bg;
        }
    }
}

// .btn:not(:disabled):not(.disabled):active, .btn:not(:disabled):not(.disabled).active {
//     box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.4);
// }

.btn-link {
    border: $none;
    box-shadow: none;
    padding: $padding-base-vertical $padding-base-horizontal;
    background: $transparent-bg;
    color: $gray-300;
    font-weight: $font-weight-bold;

    &:hover {
        box-shadow: none !important;
        transform: none !important;
    }
}

.btn-lg {
    @include btn-size(
        $btn-padding-y-lg,
        $btn-padding-x-lg,
        $font-size-base,
        $border-radius-lg
    );
}

.btn-sm {
    @include btn-size(
        $btn-padding-y-sm,
        $btn-padding-x-sm,
        $font-size-base,
        $border-radius-sm
    );
}

.btn-wd {
    min-width: 140px;
}

.btn-group.select {
    width: 100%;
}

.btn-group {
    .btn.active {
        box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.4);
        transform: translateY(-1px);
        -webkit-transform: translateY(-1px);
    }

    label.btn.active {
        transform: translateY(0);
        -webkit-transform: translateY(0);
    }
}

.btn-group.select .btn {
    text-align: left;
}

.btn-group.select .caret {
    position: absolute;
    top: 50%;
    margin-top: -1px;
    right: 8px;
}

.btn-round {
    border-width: $border-thin;
    border-radius: $btn-round-radius;

    &.btn-simple {
        padding: ($padding-btn-vertical - 1) ($padding-round-horizontal - 1);
    }
}

.no-caret {
    &.dropdown-toggle::after {
        display: none;
    }
}

.btn {
    // social buttons
    &.btn-facebook {
        @include social-buttons-color($facebook, $facebook-states);
    }

    &.btn-twitter {
        @include social-buttons-color($twitter, $twitter-states);
    }

    &.btn-pinterest {
        @include social-buttons-color($pinterest, $pinterest-states);
    }

    &.btn-google {
        @include social-buttons-color($google-plus, $google-plus-states);
    }

    &.btn-linkedin {
        @include social-buttons-color($linkedin, $linkedin-states);
    }

    &.btn-dribbble {
        @include social-buttons-color($dribbble, $dribbble-states);
    }

    &.btn-github {
        @include social-buttons-color($github, $github-states);
    }

    &.btn-youtube {
        @include social-buttons-color($youtube, $youtube-states);
    }

    &.btn-instagram {
        @include social-buttons-color($instagram, $instagram-states);
    }

    &.btn-reddit {
        @include social-buttons-color($reddit, $reddit-states);
    }

    &.btn-tumblr {
        @include social-buttons-color($tumblr, $tumblr-states);
    }

    &.btn-behance {
        @include social-buttons-color($behance, $behance-states);
    }
}

.btn-secondary:not(:disabled):not(.disabled):active,
.btn-secondary:not(:disabled):not(.disabled).active,
.show > .btn-secondary.dropdown-toggle {
    color: $white;
}

@include media-breakpoint-down(md) {
    .btn-group .btn {
        padding-left: $padding-round-horizontal;
        padding-right: $padding-round-horizontal;
    }
}
